.grid                    {display: flex; flex-direction: column;}
.grid .grid-row          {display: flex; flex-direction: row;}
.grid .grid-item         {flex: 0 0 50px; height: 50px; border: 1px solid #000;}

.grid .grid-item:not(.t-empty) {background-color: cadetblue;}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

